import React, {Component} from 'react';
import './Projects.css';
import Project from '../Project';
import { projectData } from '../utils/projectData'

export class Projects extends Component {
  constructor() {
    super()
    this.state = ({
      projectIndex: 0,
    }) 
  }

  next = () => {
    const { projectIndex } = this.state;
    
    if (projectIndex === projectData.length - 1) {
      this.setState({ projectIndex: 0 })
    } else {
      this.setState({ projectIndex: projectIndex + 1 })
    }
  }

  back = () => {
    const { projectIndex } = this.state;
    
    if (projectIndex === 0) {
      this.setState({ projectIndex: projectData.length - 1 })
    } else {
      this.setState({ projectIndex: projectIndex - 1 })
    }
  }

  render() {
    return (
      <div id='projects' className='pp-container'>
        <div className='pp-projects-container'>
          <img 
            onClick={this.back}
            className='pp-back-icon pp-icon' src={require('../utils/assets/back.svg')}/>
          <Project data={projectData[this.state.projectIndex]} />
          <img 
            onClick={this.next}
            className='pp-next-icon pp-icon' src={require('../utils/assets/next.svg')}/>
        </div>
      </div>
    )
  }
}

export default Projects;