import React from 'react';
import './Project.css';

const Project = (props) => {
  const { name, description, technologies, url, github, image, spec } = props.data;
  const technologiesIcons = technologies.map(technology => {
    return (
      <img 
        className='p-tech-icon'
        src={require(`../utils/assets/${technology}`)}
        alt='tech icon'
        key={technology}
      />
    )
  })

  return (
    <div className='p-container'>
      <h1 className='p-title'>{name}</h1>
      <img 
        className='p-project-img' 
        src={require(`../utils/assets/${image}-laptop.png`)} />
        <div className='p-btns'>
          <a className='p-btn' target="_blank" href={url}>live</a>
          <a className='p-btn' target="_blank" href={github}>repo</a>
          <a className='p-btn' target="_blank" href={spec}>spec</a>
        </div>
      <p>{description}</p>
      <div>
        <h3>Tech Stack:</h3>
          { technologiesIcons }
      </div>
    </div>
  )
}

export default Project;